<template>
  <div class="generate">
    <span v-for="(item,index) in colors" :key="'g2-'+index">
        {{item.short}},<br>
    </span><br>
    <span v-for="(item,index) in colors" :key="'g3-'+index">
        <span v-for="(item2,index2) in normal" :key="index2">
          {{item.short}}{{item2}},
          <span v-for="(item3,index3) in 9" :key="'g4-'+index3">
            <!--.bg-{{item.short}}{{item2}}{{item3}}{background-color:@{{item.short}}{{item2}}{{item3}};}<br>-->
          </span>
          <br>
        </span>
        <br>
      </span>
    <hr>
    <span v-for="(item,index) in colors" :key="'g5-'+index">
        {{item.short}}=chroma(basered).set("hsl.h",{{index*15}}).hex(),<br>
    </span>
    <hr>
    <span v-for="(item,index) in colors" :key="'g6-'+index">
        <span v-for="(item2,index2) in normal" :key="'g7-'+index2">
          {{item.short}}{{item2}}=chroma({{item.short}}).set("hsl.s",$c{{item2}}).hex(),
          <span v-for="(item3,index3) in 9" :key="index3">
            <!--.bg-{{item.short}}{{item2}}{{item3}}{background-color:@{{item.short}}{{item2}}{{item3}};}<br>-->
          </span>
          <br>
        </span>
        <br>
      </span>
      <hr>
      <span v-for="(item,index) in colors" :key="'g8-'+index">
        <span v-for="(item2,index2) in normal" :key="'g9-'+index2">
          <span v-for="(item3,index3) in 9" :key="'g0-'+index3">
            <span>
              {{item.short}}{{item2}}{{item3}}:chroma({{item.short}}{{item2}}).set("hsl.l",$level{{item3}}).hex(),<br>
            </span>
            
          </span>
          <br>
        </span>
      </span>
    </div>
</template>

<script>
export default {
  data() {
    return {
      colors:[
        {short:"re",eng:"red",chs:""},
        {short:"ro",eng:"red-orange",chs:""},
        {short:"or",eng:"orange",chs:""},
        {short:"oy",eng:"orange-yellow",chs:""},
        {short:"ye",eng:"yellow",chs:""},
        {short:"yc",eng:"yellow",chs:""},
        {short:"ch",eng:"chartreuse",chs:""},
        {short:"cg",eng:"chartreuse-green",chs:"-"},
        {short:"gr",eng:"green",chs:""},
        {short:"ga",eng:"green-aquamarine",chs:"-"},
        {short:"aq",eng:"aquamarine",chs:""},
        {short:"ac",eng:"aquamarine-cyan",chs:"-"},
        {short:"cy",eng:"cyan",chs:""},
        {short:"cb",eng:"cyan-blue",chs:""},
        {short:"bl",eng:"blue",chs:""},
        {short:"bi",eng:"blue-indigo",chs:""},
        {short:"in",eng:"indigo",chs:""},
        {short:"ip",eng:"indigo-purple",chs:""},
        {short:"pu",eng:"purple",chs:""},
        {short:"pp",eng:"purple-pink",chs:""},
        {short:"pi",eng:"pink",chs:""},
        {short:"pv",eng:"pink-violetred",chs:"-"},
        {short:"vi",eng:"violetred",chs:""},
        {short:"vr",eng:"violetred-red",chs:"-"},
        //{short:"gy",eng:"gray",chs:""}
      ],
      normal:["l","m","r","d"],
      variation:["hl","hm","hr","hd"]//variation
    }
  },
  methods:{
  },
  mounted(){

  }
}
</script>
